
h1, h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}
.detector {
  text-align: center;
}
.card {
  width: 80%;
  max-width: 800px;
  margin: 20px auto;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  overflow: hidden;
  font-family: 'Microsoft YaHei UI Light';
  position: relative;
}
.title {
  background: rgba(42, 38, 94, 1);
  color: #fff;
  padding: 0 30px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}
.title h1 {
  font-size: 16px;
}
.content {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  border: 1px solid rgba(42, 38, 94, 0.4);
  border-left: 0;
  font-size: 13px;
  padding: 20px 0;
  text-align: left;
}
.detect-item{
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  color: rgba(42, 38, 94, 1);
  padding: 5px 20px;
}
.item {
  word-break: keep-all;
  padding-right: 10px;
  font-weight: bold;
  min-width: 80px;
}
.red {
  color: red;
}
#capture {
  padding: 20px;
  display: inline-block;
}
.control {
  text-align: center;
}
.btn {
  opacity: .85;
  background: rgba(42, 38, 94, 1);
  color: #fff;
  padding: 10px 20px;
  border: 0;
}
.btn:hover{
  opacity: 0.9;
}
.btn:active{
  opacity: 1;
}
.copy-btn {
  position: absolute;
  top: 2px;
  right: 2px;
  width: 20px;
  height: 20px;
  background: url(data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAABgAAAAYCAYAAADgdz34AAAACXBIWXMAAAsTAAALEwEAmpwYAAAAc0lEQVRIieWVSwqAMAxEc5kImTvZw1Y9l+LCRVf5tIWoA7Nr8+AVUqJPR7gcWMrpqXDZzADv8KduAE06Px4gvO49SqABep3DCogqwf8AcDrPB9CSH4AsbwDrsosqQts6HECzLiAKiC47Nfd3FwBUffKbcwFFTEBgJzxlvQAAAABJRU5ErkJggg==) no-repeat;
  background-size: 100%;
  cursor: pointer;
  opacity: .8;
}
.copy-btn:hover {
  opacity: .9;
}
.copy-btn:active {
  opacity: 1;
}
@media only screen and (max-width: 600px) {
.card {
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
        -ms-flex-direction: column;
            flex-direction: column;
    width: 100%;
}
.detect-item {
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
        -ms-flex-direction: column;
            flex-direction: column;
}
.detect-item .item {
    padding-bottom: 5px;
}
.content {
    border-left: 1px solid rgba(42, 38, 94, 0.4);
    padding: 10px 0;
}
.copy-btn {
    top: unset;
    bottom: 10px;
    right: 10px;
}
}
